import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const { getTestList, submit } = config.api;
export default {
	data() {
		return {
			testList: [],
			dataList:[],
			//清空选择框
			checkBox:true
		};
	},
	onLoad() {
		this.getTest();
	},
	methods: {
		getTest() {
			var that = this;
			let infoOpt = {
				url: getTestList,
				type: 'POST',
				data: {
					id: '',
					title: '',
					pageNum: 1,
					pageSize: 999
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res.list);
				res.list.forEach(res => {
					res.content = JSON.parse(res.content);
				});
				that.testList = res.list;
			};
			sendAjax(infoOpt, infoCb);
		},
		CheckboxChange(e) {
			this.checkBox = true
			this.dataList[e.target.dataset.index] = {
				value:e.detail.value,
				answer:e.target.dataset.answer
			}
		},
		sub(){
			let that = this
			let newList = []
			that.dataList.forEach(res=>{
				newList.push(res)
			})
			if(newList.length!=that.testList.length){
				uni.showModal({
					title: '提示',
					content: `您还有${that.testList.length-newList.length}题未答，请继续答题！`,
					showCancel: false
				});
			}else{
				let d = 0
				let c = 0
				that.dataList.forEach(res=>{
					if(JSON.stringify(res.value)===res.answer){
						d++
					}else{
						c++
					}
				})
				if(d/(that.dataList.length)>=0.8){
					let infoOpt = {
						url: submit,
						type: 'POST',
						data: {
							id: uni.getStorageSync('userInfo').userDetail.id,
							state: true,
						}
					};
					let infoCb = {};
					infoCb.success = function(res) {
						uni.showModal({
							title: '恭喜',
							content: '恭喜你通过考核！',
							showCancel: false
						});
						uni.setStorageSync('pasttest',1)
					};
					sendAjax(infoOpt, infoCb);
				}else{
					uni.showModal({
						title: '遗憾',
						content: `很遗憾您答错${c}题，未通过考核！`,
						showCancel: false,
						complete: function (res) {
							console.log(res)
						        if (res.confirm) {
						            console.log('用户点击确定');
									that.checkBox = false
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }

					});
				}
			}
			
		}
	}
};
